body {
    background: #d1defa;
    font-family: sans-serif, Arial, Helvetica;
    text-align: center;
    padding: 0px;
    margin: 0px;
}

.layout {
    width: 100%;
    height: auto;

    display: grid;
    grid-template-areas:
        "header header header header"
        "nav nav nav nav"
        "content content content lateral"
        "footer footer footer footer"
    ;
    grid-template-rows: 100px 70px auto 70px;
}

/*Header Styles*/

.header {
    grid-area: header;

    background: white;
    text-align: left;
    font-style: italic;

    display: flex;
    justify-content: center;
}

.header h1 {
    color: rgba(0, 0, 0, 0.9);
}

.peli-item .edit:hover {
    background: #62ffe2;
}

.peli-item .delete:hover {
    background: #f261ff;
}

.logo {
    background: linear-gradient(90deg, rgb(98, 255, 226) 10%, rgb(242, 97, 255));
    width: 40px;
    height: 40px;
    border-radius: 300px;
    margin-top: 28px;
}

.play {
    width: 0;
    height: 0;
    margin-top: 15px;
    margin-left: 16px;
    border-left: 12px solid white;
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent;
}

.header h1 {
    margin-left: 15px;
    margin-top: 30px;
}

/*Nav Styles*/
.nav {
    grid-area: nav;
    background: rgba(0, 0, 0, 0.9);
}

.nav>ul {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
}

.nav li {
    padding: 10px;
}

.nav a {
    display: block;
    margin-left: 30px;
    margin-right: 30px;
    font-size: 18px;
    font-weight: lighter;
    color: rgba(255, 255, 255, 0.9);
    text-decoration: none;
    transition: all 300ms;
}

.nav a:hover {
    transform: scale(1.1, 1.1);
    color: rgba(255, 255, 255, 1);
}

/*PrincipalContent styles*/
.content {
    grid-area: content;
    background: #f6f7f9;
    padding-top: 50px;

    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
    align-items: flex-start;
    flex-grow: 1;
}

.peli-item {
    flex-grow: 1;
    background: white;
    border-radius: 10px;
    box-shadow: 0px 0px 6px #ddd;
    margin: 20px;
    padding-top: 60px;
    padding-bottom: 60px;
    padding-left: 25px;
    padding-right: 25px;
}

button {
    margin: 0px 2px;
}

button,
input[type="submit"],
input[type="button"] {
    width: 100px;
    background: rgba(0, 0, 0, 0.8);
    color: white;
    border: 0;
    outline: 0;
    padding: 15px;
    border-radius: 3px;
    margin-top: 15px;
    text-transform: capitalize;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    transition: all 300ms;
}

.peli-item .edit:hover {
    background: #62ffe2;
}

.peli-item .delete:hover {
    background: #f261ff;
}

/*SideBar Styles*/
.lateral {
    grid-area: lateral;
    background: #f6f7f9;
    border-left: 1px solid #ddd;
    padding: 20px;
    padding-top: 50px;
    padding-bottom: 50px;
}

.lateral>div {
    box-sizing: border-box;
    width: 90%;
    margin: 20px;
    padding: 20px;
    padding-top: 10px;
    background: white;
    border-radius: 10px;
    box-shadow: 0px 0px 6px #ddd;
}

.lateral input[type="text"],
.lateral input[type="text"],
.lateral textarea,
.edit_form input[type="text"],
.edit_form input[type="text"],
.edit_form textarea {
    display: block;
    margin: 0 auto;
    margin-top: 15px;
    padding: 12px;
    border-radius: 5px;
    background: #f6f7f9;
    color: #454545;
    width: 80%;
    opacity: 4;
    outline: 0;
    border-color: transparent;
}

.edit_form {
    border-top: 1px solid #ddd;
    margin-top: 10px;
}

#description2, #title2{
    display: block;
    text-align: center;
    margin: 0 auto;
    margin-top: 15px;
    padding: 12px;
    border-radius: 5px;
    background: #f6f7f9;
    color: #454545;
    width: 80%;
    opacity: 4;
    outline: 0;
    border-color: transparent;
}

/*Footer Styles*/
.footer {
    grid-area: footer;

    background: rgba(0, 0, 0, 0.9);
    color: rgba(255, 255, 255, 0.9);
    border-top: 1px solid rgba(255, 255, 255, 0.9);
    text-align: center;
    line-height: 68px;
    padding-left: 25px;
}

.footer a {
    background: linear-gradient(90deg, #62ffe2, #f261ff);
    font-size: 17px;
    text-decoration: none;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
}

@media (max-width: 1085px) {
    .content {
        flex-flow: column wrap;
        justify-content: center;
        align-items: center;
    }

    .peli-item {
        width: 80%;
    }
}

@media (max-width: 700px) {
    .peli-item {
        width: 70%;
    }
}

@media (max-width: 435px) {
    .content {
        padding-top: 80px;
        padding-bottom: 80px;
    }
    .lateral{
        padding-top: 80px;
        padding-bottom: 80px;
    }
}